import superagentPromise from 'superagent-promise';
import _superagent from 'superagent';
import jwt from './jwt';

const superagent = superagentPromise(_superagent, global.Promise);

const API_ROOT = 'http://api.davidcoleman.tech:3001/api';
// const API_ROOT = 'http://localhost:3001/api';

console.log('using API at ',API_ROOT);

const responseBody = res => res.body;

const requests = {
    get: url =>
        superagent.get(`${API_ROOT}${url}`).use(jwt.tokenPlugin).then(responseBody),
    post: (url, body) =>
        superagent.post(`${API_ROOT}${url}`, body).use(jwt.tokenPlugin).then(responseBody)
};

export default requests;



