import React from 'react';
import { connect } from 'react-redux';
import './home.css';

const mapStateToProps = state => ({
  articles: state.home.articles
});

const SimpleHomeView = props => {
  return (
    <div className="simple-home-view">
      <div className="header">Welcome to davidcoleman.tech!</div>
      <div className="intro">
        This site is built with React.js/Redux, using a node/express/mongo backend.
        <br/>
        This site is under construction, so it will be changing frequently over the
        next few weeks or so!
        <br/>
        Check me out on LinkedIn: 
        <a href="https://www.linkedin.com/in/davidkcoleman/" target="_blank">
          https://www.linkedin.com/in/davidkcoleman/
        </a>
        <br/>
        Follow me on github:
        <a href="https://github.com/davidcoleman007" target="_blank">
          https://github.com/davidcoleman007
        </a>
      </div>
    </div>
  );
};

export default connect(mapStateToProps, () => ({}))(SimpleHomeView);




