import React from 'react';
import { connect } from 'react-redux';

import './styles.css';

const mapStateToProps = state => ({
    appName : state.common.appName,
    resume  : state.resume,
    log     : console.log('Projects state',state)
});

const mapDispatchToProps = dispatch => ({
});

class Projects extends React.Component {
    componentWillMount() {
       console.log('Projects props',this.props);
    }

    render() {
        var Projects;
        var content = null;
        console.log(this.props.resume);
        var companyName = '';
        var projects = [];
        if(this.props.resume.selectedJob && this.props.resume.selectedJob !== null) {
            projects = (
                <div className="list">
                    {this.props.resume.selectedJob.projects.map(
                        (project, pIdx) => {
                            return (
                                <div className="project" key={pIdx}>
                                    <div className="header">
                                        <h5 className="name">{project.name}</h5>
                                        {(project.url !='') &&
                                            <a className="url" href={project.url} target="_blank">{project.url}</a>
                                        }
                                        
                                    </div>
                                    <div className="duties">
                                        <ul>
                                            {project.duties.map(
                                                (duty, dIdx) => {
                                                    return (
                                                        <li key={dIdx}>{duty}</li>
                                                    );
                                                }
                                            )}
                                        </ul>
                                    </div>
                                </div>
                            );
                        }
                    )}
                </div>
            );
        }
        return (
            <div className="projects">
                <h4>Projects:</h4>
                {projects}
            </div>
        );
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Projects);



