import React from 'react';
import { connect } from 'react-redux';
import Projects from './Projects';
import TagList from '../TagList';
import './styles.css';

const mapStateToProps = state => ({
  appName: state.common.appName,
  resume : state.resume,
  log : console.log('JobContent state',state)
});

const mapDispatchToProps = dispatch => ({
});

class JobContent extends React.Component {
  componentWillMount() {
    console.log('JobContent props',this.props);
  }

  render() {
    console.log(this.props.resume);
    //  eventually let's NOT do this if we have a job selected.  It's extra processing that's not necessary
    var jobContent;
    var content = null;
    console.log(this.props.resume);
    var companyName = '';
    var duties = [];
    var title = '';
    if(this.props.resume.selectedJob && this.props.resume.selectedJob !== null) {
        companyName = this.props.resume.selectedJob.name;
        duties = (
            <ul>
                {this.props.resume.selectedJob.general.map(
                    (duty, idx) => {
                        return <li key={idx}>{duty}</li>;
                    }
                )}
            </ul>
        );
        title = this.props.resume.selectedJob.role;
    }
    return (
        <div className="job">
            <h2>{companyName}</h2>
            <h3>{title}</h3>
            <h4>Skills</h4>
            <TagList />
            <h4>Responsibilities:</h4>
            {duties}
            <Projects/>
        </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(JobContent);



